
.background {
    width: 100vw;
    height: 150vh;
    background-image: url('./peinture-romaine.jpg');
    background-position:bottom;
    background-size: cover;
    position: fixed;
    top: 0;
    left: 0;
    z-index: -1;
   
}
.background-black {
    width: 100vw;
    height: 500vh;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}
.background-end {
    width: 100vw;
    height: 300vh;
    position: absolute;
    top: 500vh;
    left: 0;
    z-index: -1;
    background-color: rgb(23, 23, 23);
    @media screen and (max-width:500px) {
        height: 250vh;
       }
}